import React from "react";
import { Pagination, PaginationItem, PaginationLink } from "reactstrap";
import { useHistory } from "react-router-dom";

import "./MyPagination.css";

export default function MyPagination({ url, startPage, page, limit, total }) {
  const history = useHistory();

  return (
    <Pagination>
      <PaginationItem disabled={page === 1}>
        <PaginationLink
          first
          onClick={() => {
            history.push(`${url}startPage=1&page=1&limit=${limit}`);
          }}
        />
      </PaginationItem>
      <PaginationItem disabled={page === 1}>
        <PaginationLink
          previous
          onClick={() => {
            history.push(
              `${url}startPage=${
                page <= startPage ? startPage - 1 : startPage
              }&page=${page - 1}&limit=${limit}`
            );
          }}
        />
      </PaginationItem>
      <PaginationItem active={page === startPage} disabled={total <= 0}>
        <PaginationLink
          onClick={() => {
            history.push(
              `${url}startPage=${startPage}&page=${startPage}&limit=${limit}`
            );
          }}
        >
          {startPage}
        </PaginationLink>
      </PaginationItem>
      <PaginationItem
        active={page === startPage + 1}
        disabled={(startPage + 1) * limit - total >= limit}
      >
        <PaginationLink
          onClick={() => {
            history.push(
              `${url}startPage=${startPage}&page=${
                startPage + 1
              }&limit=${limit}`
            );
          }}
        >
          {startPage + 1}
        </PaginationLink>
      </PaginationItem>
      <PaginationItem
        active={page === startPage + 2}
        disabled={(startPage + 2) * limit - total >= limit}
      >
        <PaginationLink
          onClick={() => {
            history.push(
              `${url}startPage=${startPage}&page=${
                startPage + 2
              }&limit=${limit}`
            );
          }}
        >
          {startPage + 2}
        </PaginationLink>
      </PaginationItem>
      <PaginationItem disabled={(page + 1) * limit - total >= limit}>
        <PaginationLink
          next
          onClick={() => {
            history.push(
              `${url}startPage=${
                page >= startPage + 2 ? startPage + 1 : startPage
              }&page=${page + 1}&limit=${limit}`
            );
          }}
        />
      </PaginationItem>
      <PaginationItem disabled={(page + 1) * limit - total >= limit}>
        <PaginationLink
          last
          onClick={() => {
            history.push(
              `${url}startPage=${
                parseInt(total / limit) + (total % limit === 0 ? 0 : 1)
              }&page=${
                parseInt(total / limit) + (total % limit === 0 ? 0 : 1)
              }&limit=${limit}`
            );
          }}
        />
      </PaginationItem>
    </Pagination>
  );
}
