import React from "react";
import { Button, Modal, ModalFooter, ModalHeader, ModalBody, Input } from "reactstrap";
import { useTranslation } from "react-i18next";

export default function ConfirmModal({
    modal,
    toggle,
    headerTitle,
    bodyContent,
    handleOkButtonOnClick,
    handleCancelButtonOnClick,
    isModalDelete = false,
    titleOk = "",
    titleCancel = "",
}) {
    const { t } = useTranslation();

    return (
        <Modal isOpen={modal} toggle={toggle}>
            <ModalHeader toggle={toggle}>{headerTitle}</ModalHeader>
            <ModalBody>{bodyContent}</ModalBody>
            <ModalFooter>
                <Button
                    color={`${isModalDelete ? "danger" : "primary"}`}
                    onClick={handleOkButtonOnClick}
                >
                    {t(titleOk || "admin.common.OK")}
                </Button>
                <Button color="warning" onClick={handleCancelButtonOnClick}>
                    {t(titleCancel || "admin.common.CANCEL")}
                </Button>
            </ModalFooter>
        </Modal>
    );
}


export function InputModal({
    modal,
    toggle,
    headerTitle,
    handleOkButtonOnClick,
    handleCancelButtonOnClick,
    isModalDelete = false,
    titleOk = "",
    titleCancel = "",
}) {
    const { t } = useTranslation();
    const [text, setText] = useState('');

    return (
        <Modal isOpen={modal} toggle={toggle}>
            <ModalHeader toggle={toggle}>{headerTitle}</ModalHeader>
            <ModalBody>
                <Input value={text} onChange={e => setText(e?.target?.value)}/>
            </ModalBody>
            <ModalFooter>
                <Button
                    color={`${isModalDelete ? "danger" : "primary"}`}
                    onClick={() => handleOkButtonOnClick(text)}
                >
                    {t(titleOk || "admin.common.OK")}
                </Button>
                <Button color="warning" onClick={handleCancelButtonOnClick}>
                    {t(titleCancel || "admin.common.CANCEL")}
                </Button>
            </ModalFooter>
        </Modal>
    );
}