import React from "react";
import { useTranslation } from "react-i18next";
import { Col, Row } from "reactstrap";

import "./TableFooter.css";

import MyPagination from "components/user/MyPagination/MyPagination";

export default function TableFooter({
  currentUrl,
  currentStartPage,
  currentPage,
  currentLimit,
  currentTotal,
}) {
  const { t } = useTranslation();

  return (
    <Row className="table-footer">
      <Col lg={6}>
        {t("admin.common.DISPLAY")}&nbsp;
        <b>{(currentPage - 1) * currentLimit + 1}</b>&nbsp; -&nbsp;
        <b>
          {currentTotal < currentPage * currentLimit
            ? currentTotal
            : currentPage * currentLimit}
        </b>
        &nbsp;
        {t("admin.common.ON")} &nbsp;
        <b>{currentTotal}</b>&nbsp;
        {t("admin.common.ITEMS")}
      </Col>
      <Col lg={6} className="table-pagination">
        <MyPagination
          url={currentUrl}
          startPage={currentStartPage}
          page={currentPage}
          limit={currentLimit}
          total={currentTotal}
        />
      </Col>
    </Row>
  );
}
