import React from "react";
import { useTranslation } from "react-i18next";
import { FaSearch } from "react-icons/fa";
import {
  Button,
  Col,
  Input,
  InputGroup,
  InputGroupAddon,
  Row,
} from "reactstrap";

import "./TableTools.css";

export default function TableTools({
  currentLimit,
  currentSearchKey,
  handleLimitOnChange,
  handleSearchOnChange,
  handleSearchOnClick,
}) {
  const { t } = useTranslation();

  return (
    <Row className="table-tools">
      <Col lg={6} className="d-flex">
        <div className="align-self-center">{t("admin.common.DISPLAY")}</div>
        &nbsp;
        <div>
          <Input
            type="select"
            value={currentLimit}
            onChange={handleLimitOnChange}
          >
            <option value={8}>8</option>
            <option value={16}>16</option>
            <option value={32}>32</option>
          </Input>
        </div>
        &nbsp;
        <div className="align-self-center">{t("admin.common.ROWS")}</div>
      </Col>
      <Col lg={6} className="text-right">
        <InputGroup>
          <Input
            type="text"
            placeholder={t("admin.common.SEARCH_PLACEHOLDER")}
            value={currentSearchKey}
            onChange={handleSearchOnChange}
          />
          <InputGroupAddon addonType="prepend">
            <Button onClick={handleSearchOnClick}>
              <FaSearch /> {t("admin.common.SEARCH")}
            </Button>
          </InputGroupAddon>
        </InputGroup>
      </Col>
    </Row>
  );
}
