import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { FaFilter } from "react-icons/fa";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import SubMajorApi from "api/common/subMajor";
import { toast } from "react-toastify";

export default function TableFilter({
    currentFilter,
    handleFilterOnClick,
    handleSubMajorFilterOnChange,
}) {
    const { t } = useTranslation();
    const [listSubMajor, setlistSubMajor] = useState([]);

    useEffect(async () => {
        try {
            const listSubMajorRes = await SubMajorApi.getAll();
            if (listSubMajorRes) {
                setlistSubMajor(listSubMajorRes.contents);
            }
        } catch (err) {
            setlistSubMajor([])
        }
    }, []);

    return (
        <Form className="d-flex flex-wrap">
            <FormGroup>
                <Label for="nganhDaoTao" className="col-lg">
                    {t("admin.knowledge_list.SUB_MAJOR")}
                </Label>
                <Input
                    type="select"
                    value={
                        currentFilter.nganhDaoTao !== null
                            ? currentFilter.nganhDaoTao
                            : -1
                    }
                    onChange={handleSubMajorFilterOnChange}
                >
                    <option key={1} value="0">
                        {t("admin.knowledge_edit.CHOOSE_MAJOR_DETAIL")}
                    </option>
                    {listSubMajor.length > 0 &&
                        listSubMajor.map((item, index) => {
                            return (
                                <option key={index + 1} value={item.id}>
                                    {t("admin.knowledge_list.COURSE")} :{" "}
                                    {item.khoa} -{" "}
                                    {t("admin.knowledge_list.MAJORS_NAME")} :
                                    {` ${item.nganhDaoTao?.ten} - ${item?.nganhDaoTao?.chuongTrinhDaoTao?.maCTDT}`}
                                </option>
                            );
                        })}
                </Input>
            </FormGroup>
            &emsp;
            <FormGroup className="align-self-end">
                <Button onClick={handleFilterOnClick}>
                    <FaFilter /> {t("admin.common.FILTER")}
                </Button>
            </FormGroup>
        </Form>
    );
}
