export function exportHead() {
  const head = [];
  head.push("Code");
  head.push("Name");
  head.push("Free optional credit");
  head.push("Optional credit");
  head.push("Obligatory credit");
  head.push("Total credit");
  head.push("Course");
  head.push("Major name");
  head.push("Note");
  return head;
}

export function exportRow(data) {
  const row = [];
  row.push(data.maKKT);
  row.push(data.ten);
  row.push(data.tinChiTuChon);
  row.push(data.tinChiBatBuoc);
  row.push(data.tinChiTuChonTuDo);
  row.push(data.tongTinChi);
  row.push(data.chiTietNganh.khoa);
  row.push(data.chiTietNganh.nganhDaoTao.ten);
  row.push(data.ghiChu);
  return row;
}
