import React from "react";
import { useTranslation } from "react-i18next";
import { FaDatabase, FaSync, FaTrash } from "react-icons/fa";
import { Button, Col, Row } from "reactstrap";

import "./TableHeader.css";

export default function TableHeader({
  headerTitle,
  deleteBtnIsHidden,
  handleDeleteOnClick,
  handleExportCSVOnClick,
  handleRefreshOnClick,
}) {
  const { t } = useTranslation();

  return (
    <Row className="table-header">
      <Col lg={6} className="container">
        <div className="vertical-center">{headerTitle}</div>
      </Col>
      <Col lg={6} className="text-right">
        <Button
          color="light"
          title={t("admin.common.DELETE")}
          onClick={handleDeleteOnClick}
          className={deleteBtnIsHidden ? "d-none" : ""}
        >
          <FaTrash fill="red" />
        </Button>
        &nbsp;
        <Button
          color="light"
          title={t("admin.common.EXPORT_CSV")}
          onClick={handleExportCSVOnClick}
        >
          <FaDatabase fill="orange" />
        </Button>
        &nbsp;
        <Button
          color="light"
          title={t("admin.common.REFRESH")}
          onClick={handleRefreshOnClick}
        >
          <FaSync fill="green" />
        </Button>
      </Col>
    </Row>
  );
}
